/**
 * Component for creating a navigation UI
 */
import React from "react";
import { Link } from 'react-router-dom';
import { Menu } from 'antd';
import { HomeOutlined, ProfileOutlined, FileProtectOutlined, StarOutlined } from '@ant-design/icons';

const Nav = (props) => {
    // Current represents the name of the current route.
    const { current } = props;

    return (
        <div>
            <Menu selectedKeys={[current]} mode="horizontal">
                <Menu.Item key="home">
                    <Link to={`/`}>
                        <HomeOutlined />Home 
                    </Link>
                </Menu.Item>
                <Menu.Item key='about'>
                    <Link to='/about'>
                        <StarOutlined />About
                    </Link>
                </Menu.Item>
                <Menu.Item key='profile'>
                    <Link to='/profile'>
                        <ProfileOutlined />Profile
                    </Link>
                </Menu.Item>
                <Menu.Item key='protected'>
                    <Link to='/protected'>
                        <FileProtectOutlined />Protected
                    </Link>
                </Menu.Item>
            </Menu>
        </div>
    )
};

export default Nav;